/* global console */
import { HTMLAudioElement } from "dom";

/**
 * Audio function that says the text in the specified language.
 * @customfunction
 * @param text Text to say
 * @param language Language to say
 * @returns The text that was said.
 */
export function audio_play(text: string, language: string, gender: string = "male"): string {
  const audio: HTMLAudioElement = new Audio(`https://texttospeech.responsivevoice.org/v1/text:synthesize?text=${encodeURIComponent(text)}&lang=${language}&engine=g1&name=&pitch=0.5&rate=0.5&volume=1&key=kvfbSITh&gender=${gender}`);
  audio.play();
  return text;
}

/**
 * Writes a message to console.log().
 * @customfunction LOG
 * @param message String to write.
 * @returns String to write.
 */
export function logMessage(message: string): string {
  console.log(message);

  return message;
}

CustomFunctions.associate("AUDIO_PLAY", audio_play);
CustomFunctions.associate("LOG", logMessage);